import React from "react";
import styles from './WhereTab.module.scss';
import {If, Then} from 'react-if';
import ContinentButtons from "./components/continentButtons/ContinentButtons";

export interface IWhenTabProps {
  show: boolean;
  values: any;
  setFieldValue: any;
}

export const removeWhere = (props, where: string, onlyObject: boolean = false) => {
  const newWhere = props.values.where.filter(t => t !== where);
  if (!onlyObject)
    props.setFieldValue('where', newWhere);
  return {...props, values: {...props.values, where: newWhere}};
}


const WhereTab = (props: IWhenTabProps) => {
  return (
    <If condition={props.show}>
      <Then>
        <div className={styles.whereTab}>
          <ContinentButtons
            continents={props.values.where}
            onChange={(continent) => {
              if (props.values.where.includes(continent)) {
                props.setFieldValue('where', props.values.where.filter(t => t !== continent));
              } else {
                props.setFieldValue('where', [...props.values.where, continent]);
              }
            }}/>
        </div>
      </Then>
    </If>
  );
};

WhereTab.defaultProps = {
  show: false,
};

export default React.memo(WhereTab);
