import moment from 'moment';
import uniqWith from 'uniq-with';
import {maxPrice, minPrice} from 'data/utils/calculators';

export const valuesToUrlPath = (inputValues: any, additions: any = {}, overrides: any = {}) => {
  const currentValues = {
    ...inputValues,
    additions,
    types: [
      ...(inputValues.types ? inputValues.types : []),
      ...(additions.types ? additions.types : []),
    ],
    ...overrides,
  };
  const queryUrlParts: any = {};

  if (currentValues.year > new Date().getFullYear()) {
    queryUrlParts.year = currentValues.year;
  }
  if (currentValues.when && currentValues.when.length > 0) {
    queryUrlParts.when = uniqWith(((a, b) => a === b), currentValues.when).map(timeStamp => moment(timeStamp).format("YYMMDD")).join('-');
  }
  if (currentValues.where && currentValues.where.length > 0) {
    queryUrlParts.where = uniqWith(((a, b) => a === b), currentValues.where).map(s => s.replace(/ /g, "_")).join('-');
  }
  if (currentValues.howMany && currentValues.howMany > 1) {
    queryUrlParts.howMany = currentValues.howMany;
  }
  if (currentValues.prices && currentValues.prices.min > minPrice) {
    queryUrlParts.min = currentValues.prices.min;
  }
  if (currentValues.prices && currentValues.prices.max < maxPrice) {
    queryUrlParts.max = currentValues.prices.max;
  }
  if (currentValues.tabs && Object.values(currentValues.tabs).find(v => v)) {
    for (const key in currentValues.tabs) {
      if (currentValues.tabs[key]) {
        queryUrlParts.tab = key;
        break;
      }
    }
  }
  if (currentValues.types && currentValues.types.length > 0) {
    queryUrlParts.types = uniqWith(((a, b) => a === b), currentValues.types)
      .filter(a => !(inputValues.types && additions.types && inputValues.types.includes(a) && additions.types.includes(a)))
      .sort()
      .join("-");
  }
  let s = '';
  for (const key in queryUrlParts) {
    if (queryUrlParts[key]) {
      s += `/${key}/${queryUrlParts[key]}`;
    }
  }
  return s;
}
let url_cache = null;
let urlObj_cache = {};
export const urlPathToValue = (additions: any = {}, defailts: any = {}) => {
  const url = window.location.pathname.split("/").filter(s => s);
  const urlObject: any = {...defailts};
  try {
    if (url.length > 0) {
      for (let slashSectionIndex = 0, totalSections = url.length; slashSectionIndex < totalSections; slashSectionIndex++) {
        const urlSection = url[slashSectionIndex];
        const nextUrlSection = url[slashSectionIndex + 1];
        if (nextUrlSection) {
          slashSectionIndex++;
          if ('year' === urlSection) {
            urlObject.year = parseInt(nextUrlSection);
          } else if ('when' === urlSection) {
            urlObject.when = nextUrlSection.split("-").map(s => moment(s, 'YYMMDD').valueOf());
          } else if ('where' === urlSection) {
            urlObject.where = nextUrlSection.split("-").map(s => s.replace(/_/g, " "));
          } else if ('howMany' === urlSection) {
            urlObject.howMany = parseInt(nextUrlSection);
          } else if ('min' === urlSection) {
            if (!urlObject.prices)
              urlObject.prices = {};
            urlObject.prices.min = parseInt(nextUrlSection);
          } else if ('max' === urlSection) {
            if (!urlObject.prices)
              urlObject.prices = {};
            urlObject.prices.max = parseInt(nextUrlSection);
          } else if ('tab' === urlSection) {
            urlObject.tabs = {
              [nextUrlSection]: true
            };
          } else if ('types' === urlSection) {
            urlObject.types = nextUrlSection.split("-");
          } else {
            slashSectionIndex--;
          }
        }
      }
    }
  } catch (e) {
  }
  url_cache = window.location.pathname;
  return urlObj_cache = {...urlObject, ...additions};
}
