import React from "react";
import styles from './ContinentButtons.module.scss';
import {MDBBtn} from 'mdbreact';

export interface ISelectorProps {
  continents: string[];
  onChange: (date) => void;
}

export const continents = {
  "AF": "אפריקה",
//  "AN": "אנטארקטיקה",
  "AS": "אסיה",
  "EU": "אירופה",
  "NA": "צפון אמריקה",
  "OC": "אוסטרליה",
  "SA": "דרום אמריקה"
};
const ContinentButtons = (props: ISelectorProps) => {


  return (<div className={styles.continentButtons}>
      {Object.keys(continents).map(continent => <MDBBtn
        key={continent}
        color={null}
        active={props.continents.includes(continent)}
        onClick={() => props.onChange(continent)}>
        {continents[continent]}
      </MDBBtn>)}
    </div>
  );
};

ContinentButtons.defaultProps = {
  continents: [],
  onChange: (continent) => {
    
  },
};

export default React.memo(ContinentButtons);
