import React from "react";
import {If, Then} from 'react-if';
import PersonIcon from 'gui/components/icons/PersonIcon';
import AddCircleOutlineRoundedIcon from '@material-ui/icons/AddCircleOutlineRounded';
import RemoveCircleOutlineRoundedIcon from '@material-ui/icons/RemoveCircleOutlineRounded';

export interface IWhenTabProps {
  show: boolean;
  values: any;
  setFieldValue: any;
}

const HowManyTab = (props: IWhenTabProps) => {
  return (
    <If condition={props.show}>
      <Then>
        <div>
          <PersonIcon/> נוסעים
          <AddCircleOutlineRoundedIcon
            style={{margin: 0, marginLeft: 12, marginRight: 80, marginTop: -5, color: '#05BEC0', cursor: "pointer"}}
            onClick={() => props.setFieldValue("howMany", Math.min(props.values.howMany + 1, 20))}/>
          <span style={{
            fontWeight: 700,
            fontSize: 24,
            color: '#323232',
          }}>{props.values.howMany}</span>
          <RemoveCircleOutlineRoundedIcon
            style={{margin: 0, marginRight: 10, marginTop: -5, color: '#05BEC0', cursor: "pointer"}}
            onClick={() => props.setFieldValue("howMany", Math.max(props.values.howMany - 1, 1))}/>
        </div>
      </Then>
    </If>
  );
};

HowManyTab.defaultProps = {
  show: false,
};

export default React.memo(HowManyTab);
