import React from "react";
import FormikSearchSelector from 'gui/components/formikSearchSelector/FormikSearchSelector';
import styles from './YearSelector.module.scss';
import ArrowDropDownRoundedIcon from '@material-ui/icons/ArrowDropDownRounded';
import {components} from 'react-select';
import ArrowUpDown from 'gui/pages/mainRouter/pages/tourism/components/searchVacations/components/arrowUpDown';

export interface ISelectorProps {
  label: string;
  className?: string;
  field?: any;
}

const Option = ({children, ...restProps}) => {
  return (
    <components.Option
      className={restProps.options[restProps.options.length - 1].label === children ? "last" : ""}
      style={{
        borderRadius: 50,
        color: "black",
        backgroundColor: "transperent"
      }}
      {...restProps}>
      {children}
    </components.Option>
  );
};


const MenuList = props => (
  <components.MenuList
    style={{
      borderRadius: 50
    }}
    {...props}>
    {props.children}
  </components.MenuList>
);


const DropdownIndicator = props => {
  return (<ArrowUpDown className={styles.dropDownArrow} expand={props.selectProps.menuIsOpen}/>)
}

const YearSelector = (props: ISelectorProps) => {
  const thisYear = new Date().getFullYear();
  const years = [];
  for (let index = 0; index <= 10; index++) {
    years.push(thisYear + index);
  }
  return (
    <div className={styles.yearSelector}>
      <FormikSearchSelector
        options={years.map((year, index) => ({
          label: `${year}`,
          value: year,

        }))}
        placeholder={
          (<span>
          שנה
          <ArrowDropDownRoundedIcon/>
        </span>)
        }
        components={{MenuList, Option, DropdownIndicator}}
        {...props}
        isClearable={false}
      />
    </div>
  );
};

YearSelector.defaultProps = {};

export default React.memo(YearSelector);
