import React from 'react';
import styles from './Tourism.module.scss';
import SearchVacations from './components/searchVacations/SearchVacations';
import {
  flights_TYPE,
  hotels_TYPE,
  PRODUCT_STATUS_PUBLISHED,
  vacations_TYPE,
  vouchers_TYPE
} from 'data/redux/constants/productsConstants';
import moment from 'moment';
import {linksMap} from 'data/utils/constants';
import CombinedTypeResultsContainer from 'data/redux/containers/combinedTypeResultsContainer';
import CrispChatContainer from 'data/redux/containers/crispChatContainer';
import ScrollToTopExtended from 'gui/components/scrollToTopExtended/ScrollToTopExtended';

interface ITourismProps {

}

const resultsQuery: any = (queryOptions, {currentValues, ...props}) => {
  queryOptions.status = {
    $in: [
      PRODUCT_STATUS_PUBLISHED,
    ],
  };
  queryOptions.$and = [];
  queryOptions.$sort = {
    askingPrice: 1,
    updatedAt: -1,
  };
  if ('when' in currentValues) {
    queryOptions.$and.push({$or: []});
    for (const w of currentValues.when) {
      const startOfMonth = moment(w).startOf('month').format("YYYY-MM-DD HH:mm:ss");
      const endOfMonth = moment(w).endOf('month').format("YYYY-MM-DD HH:mm:ss");
      if ([flights_TYPE, vacations_TYPE].includes(props.type)) {
        queryOptions.$and[queryOptions.$and.length - 1].$or.push({
          originTimeOfTakeOff: {
            $gte: startOfMonth,
            $lte: endOfMonth,
          }
        });
      } else if (props.type === hotels_TYPE) {
        queryOptions.$and[queryOptions.$and.length - 1].$or.push({
          checkInDate: {
            $gte: startOfMonth,
            $lte: endOfMonth,
          }
        });
      } else if (props.type === vouchers_TYPE) {
        queryOptions.$and[queryOptions.$and.length - 1].$or.push({
          expiration: {
            $lte: endOfMonth,
          }
        });
      }
    }
  }
  if ('where' in currentValues) {
    queryOptions.$and.push({$or: []});
    for (const w of currentValues.where) {
      if ([flights_TYPE, vacations_TYPE, hotels_TYPE].includes(props.type)) {
        queryOptions.$and[queryOptions.$and.length - 1].$or.push({
          continent: w
        });
      }
    }
  }
  if ('prices' in currentValues) {
    queryOptions.$and.push({$and: []});
    if ('min' in currentValues.prices) {
      queryOptions.$and[queryOptions.$and.length - 1].$and.push({
        askingPrice: {
          $gte: currentValues.prices.min,
        }
      });
    }
    if ('max' in currentValues.prices) {
      queryOptions.$and[queryOptions.$and.length - 1].$and.push({
        askingPrice: {
          $lte: currentValues.prices.max,
        }
      });
    }
  }
  if ('howMany' in currentValues) {
    queryOptions.$and.push({});
    if ([flights_TYPE, vacations_TYPE].includes(props.type)) {
      queryOptions.$and[queryOptions.$and.length - 1].$and = {
        ticketsAmount: {
          $gte: currentValues.howMany,
        }
      };
    } else if ([hotels_TYPE].includes(props.type)) {
      queryOptions.$and[queryOptions.$and.length - 1].$and = {
        roomPeopleAmount: {
          $gte: currentValues.howMany,
        }
      };
    }
  }
  queryOptions.name = props.type ? props.type.slice(0, -1) : '';
  return queryOptions;
};

const Tourism = props => {
  return (
    <div className={styles.tourismImageContainer}>
      <CrispChatContainer/>
      <ScrollToTopExtended bottom={95} scrollOnRender smooth/>
      <div className={styles.tourismImage}>
        <SearchVacations/>
      </div>
      <CombinedTypeResultsContainer
        startingPath={linksMap.tourism}
        queryPreProcess={resultsQuery}
      />
    </div>
  );
}


export default Tourism;
