import React from "react";
import {When} from 'react-if';
import styles from './PricesTab.module.scss';
import PricesSliderContainer from 'data/redux/containers/pricesSliderContainer';
import PriceMinMaxContainer from "data/redux/containers/priceMinMaxContainer/PriceMinMaxContainer";


export interface IWhenTabProps {
  show: boolean;
  values: any;
  setFieldValue: any;
}

const PricesTab = (props: IWhenTabProps) => {
  return (
    <When condition={props.show}>
      {() =>
        <div className={styles.pricesTab}>
          <div className={`${styles.pricesSlider}`}>
            <PricesSliderContainer
              start={props.values.prices.min}
              end={props.values.prices.max}
              onChange={(start, end) => {
                props.setFieldValue('prices', {min: start.showNumber, max: end.showNumber})
              }}/>
          </div>
          <div className={`${styles.pricesMinMaxContainer}`}>
            <PriceMinMaxContainer/>
          </div>
        </div>
      }
    </When>
  );
};

PricesTab.defaultProps = {
  show: false,
};

export default React.memo(PricesTab);
