import {connect} from 'react-redux'
import SliderWithBars from 'gui/components/SliderWithBars';
import {getAskingPricesActionCreator} from 'data/redux/actions/guiActions';
import {askingPriceSelector} from 'data/redux/selectors/guiSelector';

const mapStateToProps = (state, ownProps = {}) => {

  return {
    graphLines: askingPriceSelector(state)
      //.reduce((r, a) => r.concat(a, 0), [0])
      .map((price: { askingPrice: number, count: number }, index) => ({
        showNumber: price.askingPrice || 0,
        x: index + 1,
        y: (price.count || 0) * 10,
      })),
    ...ownProps,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onLoad: () => dispatch(getAskingPricesActionCreator()),
  };
};

const PricesSliderContainer = connect(mapStateToProps, mapDispatchToProps)(SliderWithBars);

export default PricesSliderContainer;
