import {connect} from 'react-redux'
import {getLoadProductsByTypeActionCreator} from 'data/redux/actions/productsActions';
import {typeProductSelector} from 'data/redux/selectors/productSelector';
import TypeResultRows
  from 'gui/components/combinedTypeResults/components/typeResults/components/TypeResult/components/TypeResultRows';

const mapStateToProps = (state, {type, limit, queryOptions, queryResultPreProcess}) => {
  let products = typeProductSelector(type)(state);
  if (typeof queryResultPreProcess === 'function') {
    products = queryResultPreProcess(products);
  }
  return {
    products,
    type,
    limit,
    queryOptions,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onLoad: (type, limit, queryOptions) => dispatch(getLoadProductsByTypeActionCreator(type, 0, limit, queryOptions)),
  };
};

const TypeResultRowsContainer = connect(mapStateToProps, mapDispatchToProps)(TypeResultRows);

export default TypeResultRowsContainer;
