import React from "react";
import ArrowDropDownRoundedIcon from '@material-ui/icons/ArrowDropDownRounded';
import ArrowDropUpRoundedIcon from '@material-ui/icons/ArrowDropUpRounded';
import {Else, If, Then} from 'react-if';

interface IArrowUpDownProps {
  expand: boolean;
  className?: string;
}


const ArrowUpDown = (props: IArrowUpDownProps) => (
  <If condition={!!props.expand}>
    <Then>
      <ArrowDropUpRoundedIcon className={`expand ${props.className}`.trim()}/>
    </Then>
    <Else>
      <ArrowDropDownRoundedIcon className={`expand ${props.className}`.trim()}/>
    </Else>
  </If>
);

ArrowUpDown.defaultProps = {
  className: '',
};

export default React.memo(ArrowUpDown);
