import React from 'react';
import styles from "./CombinedTypeResults.module.scss";
import {flights_TYPE, hotels_TYPE, vacations_TYPE, vouchers_TYPE} from 'data/redux/constants/productsConstants';
import TypeResults from './components/typeResults/TypeResults';
import {ITypeProps} from 'gui/components/combinedTypeResults/components/typeResults/components/TypeResult';
import HtmlTags from 'react-html-tag-attributes';
import SelectType from './components/selectSearchType/SelectType';

interface ICombinedTypeResults {
  pathname?: any;
  queryPreProcess?: (query: any, props: ITypeProps) => any;
  queryResultPreProcess?: (result: []) => any;
  componentMode?: boolean;
  children?: any;
  startingPath?: string;
  singleType?: boolean;
}

const buttonTypes = {
  all: 'הכל',
  [flights_TYPE]: 'טיסות',
  [hotels_TYPE]: 'מלונות',
  [vouchers_TYPE]: 'שוברים',
  [vacations_TYPE]: 'חבילת נופש',
};

const CombinedTypeResults = (props: ICombinedTypeResults) => (
  <div itemProp="offers"
       itemType="http://schema.org/Offer"
       className={styles.typeResultsContainer}>
    <HtmlTags itemtype="https://schema.org/SomeProducts"/>
    {props.children}
    <SelectType/>
    <TypeResults
      key={props.pathname}
      startingPath={props.startingPath}
      queryPreProcess={props.queryPreProcess}
      queryResultPreProcess={props.queryResultPreProcess}
      buttonTypes={buttonTypes}/>
  </div>
);

CombinedTypeResults.defaultProps = {};

export default React.memo(CombinedTypeResults);
