import React from "react";
import {Chip} from "@material-ui/core";
import BackspaceIcon from '@material-ui/icons/Backspace';
import LinkBlockContainer from 'data/redux/containers/linkBlockContainer';

export interface IQuerySelectionProps {
  label: string;
  to: string;
  onMenuSelect: any;
}

const QuerySelection = (props: IQuerySelectionProps) => {
  return (
    <Chip
      key={props.label}
      label={props.label}
      onDelete={() => {
      }}
      style={{margin: 4}}
      deleteIcon={
        <LinkBlockContainer style={{padding: 0, paddingLeft: 30}}
                            onMenuSelect={props.onMenuSelect}
                            to={props.to}>
          <BackspaceIcon/>
        </LinkBlockContainer>
      }
    />
  );
};

QuerySelection.defaultProps = {
  show: false,
};

export default React.memo(QuerySelection);
