import {connect} from 'react-redux'
import {typeProductSelector} from 'data/redux/selectors/productSelector';
import TypeResult from 'gui/components/combinedTypeResults/components/typeResults/components/TypeResult';
import {isLoadingSelector} from 'data/redux/selectors/guiSelector';

const mapStateToProps = (state, ownProps) => {
  let products = typeProductSelector(ownProps.type)(state);
  if (typeof ownProps.queryResultPreProcess === 'function') {
    products = ownProps.queryResultPreProcess(products);
  }
  const productAmount = Array.isArray(products) ? products.length : 0
  const noProducts = productAmount <= 0;
  return {
    noProducts,
    productAmount,
    loading: isLoadingSelector(state),
    ...ownProps,
  };
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return {};
};

const TypeResultContainer = connect(mapStateToProps, mapDispatchToProps)(TypeResult);

export default TypeResultContainer;
