import React from "react";
import styles from './TabButton.module.scss';
import DateRangeIcon from '@material-ui/icons/DateRange';
import ArrowUpDown from '../arrowUpDown';


interface ITabButtonProps {
  name?: string;
  label?: string;
  values?: any,
  setFieldValue?: (name: string, obj: any) => void;
  icon?: any;
  noLeftBorder?: boolean;
}


const TabButton = ({name, label, values, setFieldValue, icon: Icon, noLeftBorder}: ITabButtonProps) =>
  (<div
    className={`${styles.TabButton}${values.tabs[name] ? ` ${styles.selected}` : ''} ${noLeftBorder ? styles.noBorder : ''}`.trim()}
    onClick={() => setFieldValue('tabs', {[name]: !values.tabs[name]})}>
    <Icon/>
    <span className={styles.searchTypeLabel}>
      {label}
                </span>
    <ArrowUpDown expand={values.tabs[name]}/>
  </div>);

TabButton.defaultProps = {
  name: '',
  label: '',
  setFieldValue: (name, obj) => {
  },
  values: {tabs: {}},
  icon: DateRangeIcon,

  noLeftBorder: false,
};

export default React.memo(TabButton);
