import React from "react";
import styles from './SearchVacations.module.scss';
import DateRangeIcon from '@material-ui/icons/DateRange';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import AttachMoneyRoundedIcon from '@material-ui/icons/AttachMoneyRounded';

import {Form, Formik} from 'formik';
import * as Yup from 'yup';
import WhenTab from './components/whenTab/WhenTab';
import WhereTab from './components/whereTab';
import PricesTab from './components/pricesTab/PricesTab';
import HowManyTab from './components/howManyTab/HowManyTab';
import {urlPathToValue} from './utils';
import {discardObjectKeys, maxPrice, minPrice} from 'data/utils/calculators';
import QuerySelections from './components/querySelections';
import SaveButton from './components/saveButton/SaveButton';
import TabButton from './components/tabButton';
import SearchButtons from "./components/searchButtons/SearchButtons";


interface ISearchVacationsProps {

}

const init = {
  tabs: {} as any,
  year: new Date().getFullYear(),
  when: [],
  where: [],
  howMany: 1,
  prices: {min: minPrice, max: maxPrice},
};

const getCurrentTabShown = values => {
  for (const tab in values.tabs) {
    if (values.tabs[tab] === true)
      return tab;
  }
  return '';
};

const isAnyTabShownCheck = values => {
  return !!getCurrentTabShown(values);
};

const closeAllTabs = async (values, setFieldValue) => {
  for (const tab in values.tabs) {
    if (values.tabs[tab] === true) {
      values.tabs[tab] = false;
    }
  }
  await setFieldValue('tabs', values.tabs, false);
};


const SearchVacations = (props: ISearchVacationsProps) => {

  const initJson = JSON.stringify(discardObjectKeys(init, (key) => ["tabs", "year"].includes(key)));
  return (
    <Formik
      initialValues={
        {
          ...init,
          ...urlPathToValue(),
        }
      }
      validationSchema={Yup.object({})}
      onSubmit={async (values, {setSubmitting}) => {
        setSubmitting(false);
        //  await props
        //    .onSubmit(values);


      }}
      enableReinitialize={true}
    >
      {({
          submitForm,
          isSubmitting,
          submitCount,
          values,
          resetForm,
          errors,
          setFieldValue,
          setValues,
          initialValues
        }) => {
        const isAnyTabShown = isAnyTabShownCheck(values);
        const currentTabShown = getCurrentTabShown(values);
        const isThereAnyChange = initJson !== JSON.stringify(discardObjectKeys(values, (key) => ["tabs", "year"].includes(key)));
        return <Form className={styles.searchForm} translate={null}>
          <div
            className={`${styles.searchInputsContainer} ${isAnyTabShown ? `${styles.expanded} expanded` : ''} ${currentTabShown}`}>
            <div className={styles.searchButtonsRow}>
              <TabButton
                values={values}
                icon={DateRangeIcon}
                setFieldValue={setFieldValue}
                name={'when'}
                label="מתי נוסעים?"
              />
              <TabButton
                values={values}
                icon={LocationOnIcon}
                setFieldValue={setFieldValue}
                name={'where'}
                label="לאן?"
              />
              <TabButton
                values={values}
                icon={AttachMoneyRoundedIcon}
                setFieldValue={setFieldValue}
                name={'prices'}
                label="טווח מחירים"
                noLeftBorder
              />
              <SearchButtons className={styles.highResSearchButton}
                             onResetClick={() => setValues({...initialValues, tabs: values.tabs})}
                             isThereAnyChange={isThereAnyChange}
                             onAnyClick={() => closeAllTabs(values, setFieldValue)} values={values}/>
            </div>
            <div className={styles.searchSelectorsRow}>
              <div
                className={styles.expandingBox}>
                <WhenTab show={values.tabs.when}
                         values={values}
                         setFieldValue={setFieldValue}
                         submitForm={submitForm}
                />
                <WhereTab show={values.tabs.where}
                          values={values}
                          setFieldValue={setFieldValue}
                />
                <PricesTab show={values.tabs.prices}
                           values={values}
                           setFieldValue={setFieldValue}
                />
                <HowManyTab show={values.tabs.howMany}
                            values={values}
                            setFieldValue={setFieldValue}
                />
              </div>
              <SaveButton onClick={() => closeAllTabs(values, setFieldValue)} show={isAnyTabShown}/>
            </div>
          </div>

          <SearchButtons className={styles.lowResSearchButton}
                         onResetClick={() => setValues({...initialValues, tabs: values.tabs})}
                         isThereAnyChange={isThereAnyChange}
                         onAnyClick={() => closeAllTabs(values, setFieldValue)} values={values}/>

          <QuerySelections show={!Object.values(values.tabs).find(tab => tab)}
                           values={values}
                           setFieldValue={setFieldValue}
                           submitForm={submitForm}
          />
        </Form>
      }}
    </Formik>
  );
};

SearchVacations.defaultProps = {};

export default React.memo(SearchVacations);
