import React, {useEffect, useState} from "react";
import {IProduct} from 'data/interfaces/products';
import Ad from 'gui/components/listPage/components/ad';
import {Else, If, Then} from "react-if";


interface ITypeProps {
  type: string;
  limit: number;
  queryOptions: any;
  products: IProduct[],
  onLoad: (type: string, limit: number, queryOptions: any) => {},
  loading?: boolean;
}

/*
<div className={styles.productsNotFound}>
          לא נמצאו מוצרים לחיפוש זה
          <br/>
          איזה כיף!
          <br/>
          זו היא הזדמנות שלך לפרסם מוצר מסוג זה ולהיות הכי ראשון בחיפוש
          <br/>
          <PublishButton/>
        </div>
 */
const TypeResultRows = ({onLoad, type, products, limit, queryOptions, ...rest}: ITypeProps) => {
  const [optionsState, setOptionsState] = useState({});
  useEffect(() => {
    if (onLoad && type) {
      onLoad(type, limit, queryOptions);
    }
  }, [onLoad, type, limit, queryOptions]);


  return (<If condition={products.length > 0}>
      <Then>
        {() => <>
          {products
            .map(product => (<Ad key={product.id} hideControl hideStatus showPurchase product={product}/>))}
        </>}
      </Then>
      <Else>
        {() => null}
      </Else>

    </If>
  );

};

TypeResultRows.defaultProps = {
  type: null,
  products: [],
  onLoad: (type: string, limit: number) => {
    
  },
};

export default React.memo(TypeResultRows);
