import {connect} from 'react-redux'
import {getAskingPricesActionCreator} from 'data/redux/actions/guiActions';
import {maxPriceSelector, minPriceSelector} from 'data/redux/selectors/guiSelector';
import PriceMinMax from 'gui/pages/mainRouter/pages/tourism/components/searchVacations/components/pricesTab/components/PriceMinMax';

const mapStateToProps = (state) => {
  return {
    minPrice: minPriceSelector(state),
    maxPrice: maxPriceSelector(state),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onLoad: () => dispatch(getAskingPricesActionCreator()),
  };
};

const PriceMinMaxContainer = connect(mapStateToProps, mapDispatchToProps)(PriceMinMax);

export default PriceMinMaxContainer;
