import React from "react";
import styles from './ScrollToSelectorOnClick.module.scss';

export interface ScrollToSelectorOnClickProps {
  className?: string;
  style?: any;
  selector: string;
  onClick?: () => void;
  children: any;
  topOffset?: number;
}

const scrollTo = (y: number) => {
  const isSmoothScrollSupported = "scrollBehavior" in document.documentElement.style;
  if (isSmoothScrollSupported) {
    // not on IE/Edge
    window.scrollTo({top: y, behavior: "smooth"});
  } else {
    // for Edge
    document.body.scrollTop = y;
    // use an offset to compensate for the fixed header
    document.documentElement.scrollTop = y;
  }
};

const getOffsetTop = (elem) => {
  let offsetTop = 0;
  do {
    if (!isNaN(elem.offsetTop)) {
      offsetTop += elem.offsetTop;
    }
  } while (elem = elem.offsetParent);
  return offsetTop;
}

const ScrollToSelectorOnClick = (props: ScrollToSelectorOnClickProps) => {
  return (
    <div
      className={`${props.className} ${styles.ScrollToSelectorOnClickContainer}`.trim()}
      style={props.style}
      onClick={() => {
        props.onClick();
        setTimeout(() => {
          const element = document.querySelector(props.selector);
          if (element) {
            const errorMessageTop = getOffsetTop(element);
            scrollTo(errorMessageTop - props.topOffset);
          }
        }, 200);
      }}>
      {props.children}
    </div>
  );
};

ScrollToSelectorOnClick.defaultProps =
  {
    className: '',
    onClick: () => {
      
    },
    children: null,
    topOffset: 180,
  };

export default ScrollToSelectorOnClick;
