import React from "react";
import {urlPathToValue} from 'gui/pages/mainRouter/pages/tourism/components/searchVacations/utils';
import TypeResultContainer from "data/redux/containers/typeResultContainer/TypeResultContainer";


interface ITypeProps {
  buttonTypes: any,
  queryPreProcess?: (query: any, props: ITypeProps) => any;
  queryResultPreProcess?: (result: []) => any;
  startingPath?: string;
  singleType?: boolean;
}

const TypeResults = (props: ITypeProps) => {
    const currentValues = urlPathToValue({}, {types: ['all']});
    return (
      <>
        {
          Object.keys(props.buttonTypes)
            .filter(key => currentValues.types.includes(key) || currentValues.types.includes('all'))
            .filter(type => type !== 'all')
            .map(key => (
                <TypeResultContainer key={key}
                                     startingPath={props.startingPath}
                                     type={key}
                                     queryPreProcess={props.queryPreProcess}
                                     queryResultPreProcess={props.queryResultPreProcess}
                                     buttonTypes={props.buttonTypes}
                />
              )
            )
        }
      </>
    )
  }
;

TypeResults.defaultProps = {buttonTypes: {}};

export default React.memo(TypeResults);
