import React from "react";
import styles from './SearchButtons.module.scss';
import {Else, If, Then} from 'react-if';
import LinkBlockContainer from 'data/redux/containers/linkBlockContainer';
import ScrollToSelectorOnClick from 'gui/components/scrollToSelectorOnClick';
import {valuesToUrlPath} from 'gui/pages/mainRouter/pages/tourism/components/searchVacations/utils';
import {discardObjectKeys} from 'data/utils/calculators';


interface ISearchButtonsProps {
  className?: string;
  onResetClick?: () => void;
  onAnyClick?: () => void;
  isThereAnyChange?: boolean;

  values?: any;

}


const ResetButton = ({onResetClick}) => <LinkBlockContainer
  className={`${styles.searchButtons} ${styles.searchButtonsRight}`}
  to={`/tourism`}
  onMenuSelect={onResetClick}>
  לאפס
</LinkBlockContainer>


const SearchButton = ({values, isThereAnyChange = false}) => <ScrollToSelectorOnClick
  selector={".selectedScroll"}
  topOffset={-20}
>
  <LinkBlockContainer className={`${styles.searchButtons} ${isThereAnyChange ? styles.searchButtonsLeft : ''}`.trim()}
                      to={`/tourism${valuesToUrlPath(discardObjectKeys(values, (key) => key === 'tabs'))}`}>
    לחפש
  </LinkBlockContainer>
</ScrollToSelectorOnClick>

const SearchButtons = ({className, values, onResetClick, onAnyClick, isThereAnyChange}: ISearchButtonsProps) =>
  (<div className={`${styles.rightSideBorder} ${className}`.trim()} onClick={onAnyClick}>
    <If
      condition={isThereAnyChange}>
      <Then>
        {() => <>
          <ResetButton onResetClick={onResetClick}/>
          <SearchButton values={values} isThereAnyChange={true}/>
        </>}
      </Then>
      <Else>
        {() => <SearchButton values={values}/>}
      </Else>
    </If>
  </div>);

SearchButtons.defaultProps = {
  className: '',
  onResetClick: () => {
  },
  onAnyClick: () => {
  },
  isThereAnyChange: false,
  values: {},
};

export default React.memo(SearchButtons);
