import React, {useEffect} from "react";
import styles from './TypeResult.module.scss';
import {MDBCol, MDBRow} from 'mdbreact';
import LinkBlockContainer from 'data/redux/containers/linkBlockContainer';
import ArrowLeftRoundedIcon from '@material-ui/icons/ArrowLeftRounded';
import {If, Then} from 'react-if';
import TypeResultRowsContainer from "data/redux/containers/typeResultRowsContainer/TypeResultRowsContainer";
import {urlPathToValue, valuesToUrlPath} from 'gui/pages/mainRouter/pages/tourism/components/searchVacations/utils';
import Ad from 'gui/components/listPage/components/ad';
import SkeletonPlaceHolder from "gui/components/skeletonPlaceHolder/SkeletonPlaceHolder";

export interface ITypeProps {
  type: string;
  buttonTypes: any,
  query: any,
  currentValues?: any;
  queryPreProcess?: (query: any, props: ITypeProps) => any;
  queryResultPreProcess?: (result: []) => any;
  onLoad: () => {},
  noProducts?: boolean;
  startingPath?: string;
  loading?: boolean;
  productAmount?: number;
}


const TypeResult = ({onLoad, noProducts, productAmount, loading, ...props}: ITypeProps) => {
  const currentValues = urlPathToValue({}, {types: ['all']});
  useEffect(() => {
    if (onLoad) {
      onLoad();
    }
  }, [onLoad]);
  const viewAllButtonActive = (currentValues.types.length > 1 || currentValues.types.includes('all'));
  const queryOptions = props.queryPreProcess({...props.query}, {...props, currentValues} as any);
  return (
    <>
      <MDBRow className={(noProducts && !loading) ? styles.noProducts : ''} style={{marginTop: 30}}>
        <MDBCol sm='6'>
          <h4 className={styles.topRow}>
            {props.buttonTypes[props.type]}
          </h4>
        </MDBCol>
        <MDBCol sm='6' style={{paddingLeft: 0, paddingRight: 0}}>
          {loading ? <SkeletonPlaceHolder
              variant="text"
              style={{float: 'left', marginLeft: 13}}
              width={100}/>
            :
            <LinkBlockContainer key={props.type} active={currentValues.types.includes(props.type)}
                                to={`${props.startingPath}${valuesToUrlPath(currentValues, {}, {types: [props.type]})}`}>
              <If condition={viewAllButtonActive}>
                <Then>
                  <h5 className={styles.viewAll}>
                    הצעות נוספות
                    <ArrowLeftRoundedIcon style={{marginLeft: -8}}/>
                  </h5>
                </Then>
              </If>
            </LinkBlockContainer>
          }
        </MDBCol>
      </MDBRow>
      <MDBRow className={(!loading && noProducts && viewAllButtonActive) ? styles.noProducts : ''}>
        <MDBCol>
          {loading ? <Ad loading={loading} hideControl hideStatus showPurchase/> :
            <TypeResultRowsContainer limit={viewAllButtonActive ? 4 : 40}
                                     queryResultPreProcess={props.queryResultPreProcess}
                                     queryOptions={queryOptions}
                                     type={props.type}/>}
        </MDBCol>
      </MDBRow>
    </>
  )
};

TypeResult.defaultProps = {
  buttonTypes: {},
  query: {},
  queryPreProcess: (queryOptions, {currentValues, ...props}) => {
    return queryOptions;
  },
  queryResultPreProcess: (results) => results,
  onLoad: () => {
    
  },
};

export default React.memo(TypeResult);
