import React, {useEffect, useState} from 'react';
import BarChart from "@yosefsolutions/react-svg-bar-chart";
import PrettoSlider from 'gui/components/PrettoSlider/PrettoSlider';
import {xxHash} from 'data/utils/calculators';

interface ISliderLine {
  x: number;
  y: number;
  showNumber: number;
}

interface ISliderWithBars {
  graphLines: ISliderLine[];
  onLoad?: () => void;
  onChange?: (start: any, end: any) => void;

  start: number;
  end: number;
}

const getExampleData = (max) => {
  function getRandomInt(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  const data = []

  for (let x = 1; x <= max; x++) {
    data.push({x: x, y: getRandomInt(1, 1000)})
  }
  return data;
}

const getClosest = (graphLines, to) => {
  graphLines = graphLines
    .filter(a => a.showNumber)
    .map(a => ({
      ...a,
      diff: Math.abs(a.showNumber - to)
    })).sort((a1: any, a2: any) => a1.diff - a2.diff);
  if (graphLines.length > 0)
    return graphLines[0];
  return {x: to};
};

const SliderWithBars = ({onLoad, ...props}: ISliderWithBars) => {
  const hash = xxHash(JSON.stringify(props.graphLines));
  const data = props.graphLines;
  const min = data.length > 0 ? data[0].x : 0;
  const max = data.length > 0 ? data[data.length - 1].x : 0;
  let defaultStart = min;
  let defaultEnd = max;
  useEffect(() => {
    if (onLoad) {
      onLoad();
    }
  }, [onLoad]);

  if (props.graphLines.length > 0) {
    if ('start' in props) {
      defaultStart = Math.max(min, getClosest(props.graphLines, props.start).x);
    }
    if ('end' in props) {
      defaultEnd = Math.min(max, getClosest(props.graphLines, props.end).x);
    }
  }

  const [selectedBarColor, setSelectedBarColor] = useState([{
    startX: defaultStart,
    endX: defaultEnd,
    color: "#f28f1770",
    hash,
  } as any]);
  if (selectedBarColor[0].hash !== hash) {
    setSelectedBarColor([{
      ...selectedBarColor,
      startX: defaultStart,
      endX: defaultEnd,
      color: "#f28f1770",
      hash
    }])
  }
  const valueLabelFormat = (index: number) => {
    const d = data[max - index];
    if (d && 'showNumber' in d && d.showNumber) {
      return d.showNumber;
    } else
      return Math.max(0, max - index);
  };

  return (
    <>
      <BarChart
        className="sliderChart"
        key={`${hash}_1`}
        data={data}
        dataColors={selectedBarColor}
        axisVisible={false}
        labelsVisible={false}
        barsColor={"rgba(186,173,157,0.44)"}
        gridVisible={false}
        barsMargin={0}
        viewBoxHeight={50}
        pointsIsHoverOnZone={true}
      />
      <PrettoSlider
        className="sliderLine"
        key={`${hash}_2`}
        style={{marginTop: -15}}
        valueLabelFormat={valueLabelFormat}
        value={[max - defaultStart + 1, max - defaultEnd + 1]}
        onChange={(event, value) => {
          const s = max - Math.max(value[0] - 1, value[1] - 1);
          const e = max - Math.min(value[0] - 1, value[1] - 1);
          if (selectedBarColor[0].startX !== s || selectedBarColor[0].endX !== e) {
            setSelectedBarColor([{
              startX: s,
              endX: e,
              color: "#f28f1770",
              hash
            }]);
            props.onChange(data[Math.max(s - 1, 0)], data[Math.min(e - 1, data.length - 1)]);
          }
        }}
        min={min}
        max={max}
        valueLabelDisplay="auto"
      />
    </>
  );
};

SliderWithBars.defaultProps =
  {
    graphLines: getExampleData(19),
    start: 0,
    end: 9999999,
    onLoad: () => {
    },
    onChange: (start, end) => {
      
    },
  };

export default React.memo(SliderWithBars);
