import React from "react";
import styles from './MonthButtons.module.scss';
import {MDBBtn} from 'mdbreact';
import moment from 'moment';

export interface ISelectorProps {
  year: number;
  dates: number[];
  onChange: (date) => void;
}


const MonthButtons = (props: ISelectorProps) => {
  const d = new Date();
  const today = new Date(d.getFullYear(), d.getMonth(), 1, 0, 0, 0, 0).getTime();
  const m = new Date(props.year, 0, 1, 0, 0, 0, 0);
  const months = [];
  for (let i = 0; i < 12; i++) {
    m.setMonth(i)
    months.push((
      <MDBBtn
        key={moment(m).format('MMM')}
        color={null}
        active={props.dates.includes(m.getTime())}
        disabled={today > m.getTime()}
        onClick={((d) => () => props.onChange(d))(new Date(m))}>
        {moment(m).format('MMM')}
      </MDBBtn>));
  }
  return (<div className={styles.monthButtons}>
      {months}
    </div>
  );
};

MonthButtons.defaultProps = {
  year: new Date().getFullYear(),
  dates: [],
  onChange: (date) => {
    
  },
};

export default React.memo(MonthButtons);
