import React from "react";

interface IPriceMinMaxProps {
  minPrice: number;
  maxPrice: number;

}


const PriceMinMax = (props: IPriceMinMaxProps) => (
  <>
    <small style={{float: "right", fontSize: 12, color: '#6E6E6E'}}>{[props.maxPrice, '₪'].join(" ")}</small>
    <small style={{float: "left", fontSize: 12, color: '#6E6E6E'}}>{[props.minPrice, '₪'].join(" ")}</small>
  </>
);

PriceMinMax.defaultProps = {};

export default React.memo(PriceMinMax);
