import React, {useState} from "react";
import TypeSelectType from "./components/typeSelector/TypeSelectType";
import styles from './SelectType.module.scss';
import SearchInput from './components/searchInput';

interface ITypeProps {
  buttonTypes: any;
  componentMode?: boolean;
  onClick?: (type) => {};
  startingPath?: string;
  singleType?: boolean;
}

const SelectType = (props: ITypeProps) => {
  const [type, setType]: any = useState();
  return (
    <div className={`${styles.typeButtons}`}>
      <TypeSelectType onChange={type => setType(type)}/>
      <SearchInput type={type}/>
    </div>
  )
};
SelectType.defaultProps = {
  buttonTypes: {},
};

export default React.memo(SelectType);
