import React from "react";
import styles from './QuerySelections.module.scss';
import {When} from 'react-if';
import moment from 'moment';
import {removeWhen} from 'gui/pages/mainRouter/pages/tourism/components/searchVacations/components/whenTab';
import {valuesToUrlPath} from 'gui/pages/mainRouter/pages/tourism/components/searchVacations/utils';
import {removeWhere} from 'gui/pages/mainRouter/pages/tourism/components/searchVacations/components/whereTab';
import {continents} from 'gui/pages/mainRouter/pages/tourism/components/searchVacations/components/whereTab/components/continentButtons';
import {maxPrice, minPrice} from 'data/utils/calculators';
import QuerySelection from './components/QuerySelection/QuerySelection';

export interface IWhenTabProps {
  show: boolean;
  values: any;
  setFieldValue: any;
  submitForm: any;
}

const QuerySelections = (props: IWhenTabProps) => {
  return (
    <When condition={props.show}>
      <div className={`${styles.querySelectorsContainer} selectedScroll`}>
        <When condition={props.values.when && props.values.when.length > 0}>
          {() => props.values.when.map(when =>
            <QuerySelection
              key={when}
              label={moment(when).format("MM/YY")}
              onMenuSelect={() => removeWhen(props, when)}
              to={`/tourism${valuesToUrlPath(removeWhen(props, when, true).values)}`}
            />
          )}
        </When>
        <When condition={props.values.where && props.values.where.length > 0}>
          {() => props.values.where.map(where =>
            <QuerySelection
              key={where}
              label={continents[where]}
              onMenuSelect={() => removeWhere(props, where)}
              to={`/tourism${valuesToUrlPath(removeWhere(props, where, true).values)}`}
            />
          )}
        </When>
        <When condition={props.values.prices.min > minPrice || props.values.prices.max < maxPrice}>
          {() => <QuerySelection
            key={[props.values.prices.min, props.values.prices.max].map(p => `${p}₪`).reverse().join(" - ")}
            label={[props.values.prices.min, props.values.prices.max].map(p => `${p}₪`).reverse().join(" - ")}
            onMenuSelect={() => props.setFieldValue('prices', {
              min: minPrice,
              max: maxPrice
            }, false)}
            to={`/tourism${valuesToUrlPath(props.values, undefined, {
              prices: {
                min: minPrice,
                max: maxPrice
              }
            })}`}
          />}
        </When>
        <When condition={props.values.howMany > 1}>
          {() => <QuerySelection
            key={['נוסעים', props.values.howMany].join(" ")}
            label={['נוסעים', props.values.howMany].join(" ")}
            onMenuSelect={() => props.setFieldValue('howMany', 1, false)}
            to={`/tourism${valuesToUrlPath(props.values, undefined, {
              howMany: 1
            })}`}
          />}
        </When>
      </div>
    </When>
  );
};

QuerySelections.defaultProps = {
  show: false,
};

export default React.memo(QuerySelections);
