import {FormControl, MenuItem, Select} from "@material-ui/core";
import styles from './TypeSelectType.module.scss';
import React, {useState} from "react";
import {KeyboardArrowDownRounded, KeyboardArrowUpRounded} from "@material-ui/icons";
import {getTourismType, productTypes} from 'data/utils/constants';
import {Link} from 'react-router-dom';

interface ITypeSelectTypeProps {
  onChange?: (type: string) => any;
}

const SelectIcon = ({upDown}) => <span className={styles.icon}>
  <KeyboardArrowUpRounded className={styles.closedIcon}/>
  <KeyboardArrowDownRounded className={styles.openIcon}/>
</span>;

const TypeSelectType = (props: ITypeSelectTypeProps) => {
  const [open, setOpen] = useState(false);
  const types = Object.entries(productTypes);
  const type = getTourismType();
  return (
    <FormControl fullWidth className={`${styles.TypeSelectType} ${open ? styles.open : styles.closed}`}>
      <Select
        IconComponent={SelectIcon}
        value={Math.max(types.map(([type]) => type.replace(/[^a-zA-Z0-9]/g, '')).indexOf(type), 0)}
        renderValue={(value: any) => <div>{types[Math.max(value, 0)][1].name}</div>}
        variant="outlined"
        onChange={props.onChange as any}
        MenuProps={{
          className: styles.menu
        }}
        onOpen={() => {
          setOpen(true);
        }}
        onClose={() => {
          setOpen(false);
        }}
      >
        {types
          .map(([type, {url, name}], index) =>
            <MenuItem className={styles.menuItem} key={type} value={index}>
              <Link to={url}>
                {name}
              </Link>
            </MenuItem>
          )}
      </Select>
      <div className={styles.halfWhite}/>
    </FormControl>
  )
};
TypeSelectType.defaultProps = {
  buttonTypes: {},
};

export default React.memo(TypeSelectType);
