import React from "react";
import styles from "./WhenTab.module.scss";
import {Field} from 'formik';
import YearSelector from './components/yearSelector/YearSelector';
import {If, Then} from 'react-if';
import MonthButtons from './components/monthButtons';

export interface IWhenTabProps {
  show: boolean;
  values: any;
  setFieldValue: any;
  submitForm: any;
}

const addWhen = (props, milliSecondsTimeStamp: number) => {
  return props.setFieldValue('when', [...props.values.when, milliSecondsTimeStamp], false);
}
export const removeWhen = (props, milliSecondsTimeStamp: number, onlyObject: boolean = false) => {
  const newWhen = props.values.when.filter(t => t !== milliSecondsTimeStamp);
  if (!onlyObject)
    props.setFieldValue('when', newWhen, false);
  return {...props, values: {...props.values, when: newWhen}};
};

const flipWhen = (props, milliSecondsTimeStamp: number) => {
  if (props.values.when.includes(milliSecondsTimeStamp)) {
    return removeWhen(props, milliSecondsTimeStamp);
  } else {
    return addWhen(props, milliSecondsTimeStamp);
  }
};

const WhenTab = (props: IWhenTabProps) => {
  return (
    <If condition={props.show}>
      <Then>
        <div className={styles.whenTab}>
          <Field
            component={YearSelector}
            label=""
            name="year"
            onChange={(ticketsAmount) => {
              props.setFieldValue('year', ticketsAmount);
              //  if (props.submitCount > 0) {
              //    props.submitForm();
              //  }
            }}
          />
          <MonthButtons
            dates={props.values.when}
            onChange={(date) => {
              flipWhen(props, date.getTime());
            }}
            year={props.values.year}/>
        </div>
      </Then>
    </If>
  );
};

WhenTab.defaultProps = {
  show: false,
};

export default React.memo(WhenTab);
